<template>
  <div>
    <!-- 添加solution弹窗 -->
    <el-dialog :visible.sync="show.addRecruit" width="90%">
      <el-form label-width="10%">
        <el-form-item label="标题">
          <el-input v-model.trim="v.addRecruitTitle" size="mini"></el-input>
        </el-form-item>
        <el-form-item label="公司">
          <el-row>
            <!-- 选择公司 -->
            <el-col :span="6">
              <el-select v-model="v.addRecruitCompanyId" size="mini">
                <el-option v-for="(c,ci) in list.companys" :key="ci" :value="c.id" :label="c.companyName"></el-option>
              </el-select>
            </el-col>

            <el-col :span="5">
              <el-button size="mini" @click="addCompany('show')">添加公司</el-button>
            </el-col>
            <el-col :span="2">
              <span>工作地点</span>
            </el-col>
            <el-col :span="4">
              <el-input v-model.trim="v.addRecruitLocation" size="mini"></el-input>
            </el-col>

          </el-row>

        </el-form-item>
        <el-form-item label="薪资(月)">
          <el-row>
            <el-col :span="3" :offset="0">
              <el-input v-model.trim="v.addRecruitSalaryMin" size="mini"></el-input>
            </el-col>
            <el-col :span="1" :offset="0">
              ~
            </el-col>
            <el-col :span="3" :offset="0">
              <el-input v-model.trim="v.addRecruitSalaryMax" size="mini"></el-input>
            </el-col>
          </el-row>
        </el-form-item>

        <!-- 有效期 -->
        <el-form-item label="有效期">
          <el-date-picker size="mini" v-model="v.addRecruitTimes" value-format="yyyy/MM/dd" type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>

        <!-- 信息来源 -->
        <!-- addRecruitInformationFrom -->
        <el-form-item label="信息来源">
          <el-input size="mini" v-model.trim="v.addRecruitInformationFrom"></el-input>
        </el-form-item>


        <el-form-item label="内容">
          <tinymce v-model="v.addRecruitContent" :width="v.tinymceWidth"></tinymce>
        </el-form-item>
      </el-form>

      <el-row>
        <el-col :span="3" :offset="5">
          <el-button :style="'width:100%'" type="primary" @click="addRecruit('commit')">写好了</el-button>
        </el-col>
        <el-col :span="3" :offset="2">
          <el-button :style="'width:100%'">不行，我要重写</el-button>
        </el-col>
        <el-col :span="3" :offset="2">
          <el-button :style="'width:100%'">不写了</el-button>
        </el-col>
      </el-row>
    </el-dialog>

    <!-- 头部 -->
    <flatGreyHead></flatGreyHead>

    <nav-bar cur="5"></nav-bar>

    <el-button size="mini" @click="reloadCompanys() ,addRecruit('show')" style="margin:20px">添加招聘信息</el-button>

    <!-- 列表 -->
    <el-table :data="list.recruits">

      <!-- 标题 -->
      <el-table-column>
        <template slot-scope="scope">
          <router-link :to="'/cDetail?tp=recruit&id='+scope.row.id">{{scope.row.title}}</router-link>
        </template>
      </el-table-column>

      <!-- 操作按钮 -->
      <el-table-column width="200">
        <template slot-scope="scope">
          <el-button-group>
            <!-- 编辑按钮 -->
            <el-tooltip class="item" effect="dark" content="删除" placement="top">
              <i class="el-icon-edit" style="cursor: pointer" @click="editRecruit(scope.row.id)"></i>
            </el-tooltip>
            <!-- 删除按钮 -->
            <el-tooltip class="item" effect="dark" content="删除" placement="top">
              <i class="el-icon-delete" style="cursor: pointer" @click="delRecruit(scope.row.id)"></i>
            </el-tooltip>
          </el-button-group>
        </template>
      </el-table-column>

      <!-- 薪资 -->
      <el-table-column width="150">
        <template slot-scope="scope">{{scope.row.salaryMonthMin}}-{{scope.row.salaryMonthMax}}/月</template>
      </el-table-column>

      <!-- 公司 -->
      <el-table-column width="200">
        <template slot-scope="scope">{{(scope.row.company|| '') .companyName}}</template>
      </el-table-column>
      <!-- 地点 -->
      <el-table-column width="200">
        <template slot-scope="scope">{{scope.row.location}}</template>
      </el-table-column>
    </el-table>

    <!-- 分页 -->
    <el-pagination :total="page.count" :current-page="page.pageIndex" @current-change="changePage"></el-pagination>

    <!-- 添加公司弹窗 -->

    <el-dialog title="添加公司" :visible.sync="show.addCompany">
      <el-form>
        <el-form-item label="公司名称">
          <el-input v-model="editCompany.companyName" size="mini"></el-input>
        </el-form-item>

        <el-form-item label="公司描述">
          <el-input v-model="editCompany.companyDesc" size="mini"></el-input>
        </el-form-item>

        <el-form-item label="公司主页">
          <el-input v-model="editCompany.companyUrl" size="mini"></el-input>
        </el-form-item>
      </el-form>

      <el-row justify="center">
        <el-col span="5">
          <el-button @click="addCompany('confirm')">确定</el-button>
        </el-col>
      </el-row>
    </el-dialog>
  </div>
</template>
<script>
import flatGreyHead from "@/components/header/flatGreyHead";

import navBar from "@/components/header/navBar";
import tinymce from "@/components/Tinymce/index";

import { get, add, del, update } from "@/api/recruit.js";
import {
  get as c_get,
  add as c_add,
  del as c_del,
  update as c_update
} from "@/api/company.js";

export default {
  components: {
    flatGreyHead,
    navBar,
    tinymce
  },

  created() {
    this.reloadRecurits();
  },
  beforeMount() {
    //   窗口宽度
    let width = document.body.clientWidth;

    // 乘以0.9是弹窗的宽度
    width *= 0.9;

    // 再乘以0.9是label的宽度
    width *= 0.81;

    this.v.tinymceWidth = width;
  },
  methods: {
    // 添加公司方法
    addCompany(t) {
      let that = this;
      if (t == "show") {
        that.show.addCompany = true;
      }

      if (t == "close") {
        that.show.addCompany = true;
        that.editCompany.companyName = "";
        that.editCompany.companyDesc = "";
        that.editCompany.companyUrl = "";
      }

      if (t == "confirm") {
        c_add({
          companyName: that.editCompany.companyName,
          companyDesc: that.editCompany.companyDesc,
          companyUrl: that.editCompany.companyUrl
        }).then(r => {
          if (r.code == 200) {
            that.$message({
              message: "添加成功",
              type: "success"
            });

            that.reloadCompanys();
          }
        });
      }
    },

    reloadCompanys() {
      c_get().then(r => {
        if (r.code == 200) {
          this.list.companys = r.data.content;
        } else {
          this.$message({
            message: "获取公司信息失败",
            type: "error"
          });
        }
      });
    },

    // 编辑
    editRecruit(id) {
      get({ id: id }).then(r => {
        if (r.code == 200) {
          this.v.addRecruitTitle = r.data.title;
          this.v.addRecruitContent = decodeURIComponent(r.data.content);
          this.v.addRecruitTitleId = r.data.id;
          this.v.addRecruitLocation = r.data.location;
          this.v.addRecruitSalary = r.data.salary;
          this.v.salaryUnit = r.data.salaryUnit;
          this.v.linkAddress = r.data.linkAddress;
        }
      });
    },

    // 删除招聘信息
    delRecruit(id) {
      del({ id: id }).then(r => {
        this.reloadRecurits();
      });
    },

    // 参数重置
    paraReset() {
      this.v.addRecruitTitle = "";
      this.v.addRecruitContent = "";
      this.v.addRecruitTimes[0] = "";
      this.v.addRecruitTimes[1] = "";
      this.v.addRecruitLocation = "";
      this.v.addRecruitSalary = "";
      this.v.highLight = "";
    },
    changePage(p) {
      this.pageIndex = p;
      this.reloadRecurits();
    },
    //   读取招聘信息列表
    reloadRecurits() {
      get({
        pageSize: this.page.pageSize,
        pageIndex: this.page.pageIndex
      })
        .then(r => {
          if (r.code == 200) {
            this.list.recruits = r.data.content;
            this.page.count = r.data.totalElements;
          }
        })
        .catch();
    },
    addRecruit(v) {
      if (v == "show") {
        this.show.addRecruit = true;

        // 参数清空
        this.paraReset();
      }
      if (v == "commit") {
        let para = {
          title: this.v.addRecruitTitle,
          content: this.v.addRecruitContent,
          beginTime: this.v.addRecruitTimes[0],
          endTime: this.v.addRecruitTimes[1],
          location: this.v.addRecruitLocation,
          salaryMonthMin: parseInt(this.v.addRecruitSalaryMin),

          salaryMonthMax: parseInt(this.v.addRecruitSalaryMax),
          informationFrom:this.v.addRecruitInformationFrom,

          salaryUnit: this.v.salaryUnit,
          highLight: this.v.highLight,
          companyId: this.v.addRecruitCompanyId
        };

        // 内容编码
        para.content = encodeURIComponent(para.content);

        add(para)
          .then(r => {
            if (r.code == 200) {
              this.$message({
                message: "添加成功",
                type: "success"
              });
              this.show.addRecruit = false;
              // 清空参数
              this.paraReset();

              this.reloadRecurits();
            }
          })
          .catch(e => {});
      }
    }
  },

  data() {
    return {
      page: {
        pageIndex: 1,
        pageSize: 10,
        count: 0
      },

      editCompany: {
        companyName: "",
        companyDesc: "",
        companyUrl: ""
      },
      show: {
        addRecruit: false,
        addCompany: false
      },
      v: {
        addRecruitTitle: "",
        addRecruitContent: "",
        addRecruitTimes: [],
        addRecruitLocation: "",
        addRecruitSalaryMin: 0,
        addRecruitSalaryMax: 0,
        addRecruitCompanyId: "",
        addRecruitInformationFrom: "",


        tinymceWidth: 0
      },
      list: {
        //   招聘信息列表
        recruits: [],

        // 公司列表
        companys: []
      }
    };
  }
};
</script>
<style scoped>
.expire {
  cursor: "pointer";
  display: inline-block;
  width: 30px;
  height: 30px;
  background: url("../assets/images/已失效.png");
  background-size: cover;
}
</style>





