import request from '@/utils/request'
export function add(para) {
  return request({
    url: '/company/add',
    method: 'get',
    params: para
  })
}

export function get(para) {
  return request({
    url: '/company/get',
    method: 'get',
    params: para
  })
}


export function update(para) {
  return request({
    url: '/company/update',
    method: 'get',
    params: para
  })
}


export function del(para) {
  return request({
    url: '/company/del',
    method: 'get',
    params: para
  })
}



